@value mediaQuery from '../../common/size.css';
.maker {
  width: 80vw;
  height: 100%;
  color: white;
  display: flex;
  flex-direction: column;
}

.container {
  display: flex;
  flex: 1;
}

@media screen and (max-width: mediaQuery) {
  .container {
    flex-direction: column;
  }
}
